import { handleError } from '@xip-online-data/handle-error';
import { ConnectorApiClient } from '@transai/management-api-client';
import { ClusterManager } from './cluster/cluster.manager';
import { ConnectorManager } from './connector/connector.manager';
import { NodeWrapper } from './util/node-wrapper';

async function main() {
  const node = new NodeWrapper();

  if (node.cluster.isPrimary) {
    new ClusterManager(node, new ConnectorApiClient()).start().subscribe();
  } else {
    await new ConnectorManager(node).start();
  }
}

main().catch((error) => {
  handleError('Error while initializing the app', error);
});
